<template>
    <div class="agency-salesman">
        <agency-under-cell v-for="cellData in agencySalesmanArr" :cell-data="cellData" :is-replace="false" class="agency-under-cell"></agency-under-cell>
        <div v-show="showEmpty&&(!agencySalesmanArr||agencySalesmanArr.length==0)" class="empty-container">
          <empty-bg :msg="emptyMsg"></empty-bg>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import agencyUnderCell from "../../../public/agencyUnderCell.vue";
import EmptyBg from "../../../popupBox/emptyBg.vue";
import bus from '../../../../common/js/eventBus.js';

export default {
  name: "agency-salesman",
  data() {
    return {
      emptyMsg: "您还未添加业务员~",
      showEmpty: false,
      isLoadDb: false
    };
  },
  components: {
    agencyUnderCell,
    EmptyBg
  },
  activated() {
    if (this.isLoadDb) this.$store.dispatch("toggleLoading", false);
  },
  mounted() {
    bus.$on("cancel-refresh-upper-data", () => {
      if(this.agencySalesmanArr.length==0) this.showEmpty=true;
    });
    if(this.agencySalesmanArr&&this.agencySalesmanArr.length!=0){
      this.isLoadDb=true;
      this.$store.dispatch("toggleLoading", false);
      return;
    } 
    this.requestSalesData();
  },
  computed: {
    ...mapState(["userid", "openid", "path", "agencySalesmanArr"])
  },
  methods: {
    requestSalesData(){
      this.$http
      .post(this.path + "php/Route.php", {
        type: "showagentlist",
        aid: this.userid,
        roletp: 3,
        page: 0
      })
      .then(response => {
        let resultData = response.body.arr;
        if (resultData && resultData.length != 0) this.showEmpty = false;
        else this.showEmpty = true;
        this.$store.commit("UPDATA_A_SALEMAN", resultData);
        this.$store.dispatch("toggleLoading", false);
        this.isLoadDb = true;
      })
      .catch(response=>{
        //请求出错处理
        console.log(response);
        this.$store.dispatch("toggleLoading", false);
      });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.agency-salesman {
  width: 100%;

  .agency-under-cell {
    width: 100%;
    margin-bottom: 5px;
    background-color: #fff;
  }

  .empty-container {
    width: 100%;
    height: 100%;
  }
}
</style>



